/* eslint-disable */
<template>
  <div>
    <div class="step-1">
      <md-field>
        <label>Distancia em KM</label>
        <md-input v-model="distance" placeholder="Distancia em KMs" required></md-input>
      </md-field>
      <md-field>
        <label>Quilometro por litro</label>
        <md-input v-model="KmForLitro"  placeholder="Quilometros por litro" required></md-input>
      </md-field>
      <md-field>
        <label>Preço da gasolina</label>
        <md-input v-model="price" placeholder="Preço gasolina" required></md-input>
      </md-field>
      <md-button class="md-raised md-primary" type="button" v-on:click="getDistance">Vamos lá!</md-button>
      <hr >
      <p>Média de consumo de combustível: {{result.consumo}} <small>litros</small></p>
      <p>Média de quanto será gasto: R${{result.precoFinal}}</p>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Content',
  data () {
    return {
      distance: '',
      KmForLitro: '',
      price: '',
      result: {
        consumo: 0,
        precoFinal: 0
      }
    }
  },
  methods: {
    getDistance (e) {
      if (!(this.KmForLitro.length === 0) && !(this.distance.length === 0) && !(this.price.length === 0)) {
        let mediaConsumo = this.distance / this.KmForLitro
        let mediaConsumoFinal = mediaConsumo.toFixed(2)

        let precoFinal = this.price * mediaConsumoFinal
        this.result.consumo = mediaConsumoFinal
        this.result.precoFinal = precoFinal.toFixed(2)
        console.log(this.result)
      }

    }
  }
}
</script>

